body{
	padding: 0;
	margin: 0;
}

#root{
	position: relative;
    height: auto;
	min-height: 100vh;
	width: 100%;
	overflow: hidden;
}

#root-image{
	width: 100%;
	min-height: 100vh;
	overflow: hidden;
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
}

#root-image img{
	width: 100%;
	height: 100%;
}

#root-wrapper{
	background: rgba(15,12,41,0.8);
	background: -webkit-gradient(linear, left top, right top, from(rgba(15,12,41,0.8)), color-stop(rgba(48,43,99,0.8)), to(rgba(36,36,42,0.8)));
	background: -webkit-linear-gradient(left, rgba(15,12,41,0.8), rgba(48,43,99,0.8), rgba(36,36,42,0.8));
	background: linear-gradient(to right, rgba(15,12,41,0.8), rgba(48,43,99,0.8), rgba(36,36,42,0.8));
	height: auto;
	min-height: 100vh;
	width: 100%;
	padding: 50px;
	z-index: 999;
}

.forecast-wrapper, .weather-wrapper{
	padding: 0 ;
}

.input-wrapper{
	height: 36px;
}

.input-wrapper button{
	width: 85px !important;
	color: #fff !important;
	border-radius: none !important;
	margin-left: 5px !important;
	z-index: 999 !important;
}

.main-input{
	width: calc(100% - 100px)!important;
}

.main-input input{
	border: none !important;
}

.forecast-wrapper .card{
	background: #2E2E46 !important;
	border-radius: 0 !important;
	border: none !important;
	color: #fff !important;
	opacity: 0.8 !important;
}

.forecast-wrapper>.ui.card>.content>.header{
	color: #fff !important;
}

.forecast-wrapper .card .content{
	border: 0 !important;
}

.forecast-wrapper .card .content i{
	font-size: 50px !important;
}

.weather-header{
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.icon-wrapper{
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.icon-description{
	padding-top: 0px !important;
	padding-bottom: 50px !important;
}

.icon-description h4{
	font-weight: 400 !important;
	font-size: 21px !important;
	margin-bottom: 14px;
}

.icon-description p{
	font-size: 15px;
}

.weather-wrapper{
	margin: 35px 0 !important;
	color: #fff;
}

.weather-wrapper h1{
	font-size: 70px;
	font-weight: 400;
}

.weather-wrapper h2{
	font-size: 56px;
	line-height: 90px;
	font-weight: 400;
}

.weather-wrapper h4{
	font-size: 23px;
	font-weight: 400;
}

.weather-wrapper p{
	font-size: 16px;
}

.weather-wrapper h4 img{
	line-height: 32px !important;
	height: 32px !important;
	margin-left: 3px; 
}

.top-row .column{
	display: -webkit-flex;
	display: flex;
}

.icon-wrapper img{
	width: 80px;
	height: 80px;
}

.header-wrapper .header, .header-wrapper{
	padding-bottom: 0 !important;
	margin-bottom: 0 !important;
}

.purple{
	color: #4A0E3F;
}

.red{
	color: #99382A;
}

.fetching-wrapper{
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center !important;
	        align-items: center !important;
	height: calc(100vh - 136px);
	margin: 0 !important;
	font-size: 20px;
}
/*==========  Desktop First Method  ==========*/

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {
	#root-image{
		height: 100%;
	}

	#root-image img{
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
	.header{
		font-size: 16px !important;
	}

	.icon-description h4, .icon-description p{
		font-size: 14px !important;	
	}

	.weather-header{
		font-size: 18px !important;
	}

	.weather-descr{
		font-size: 14px !important;
		font-weight: 700 !important;
	}

}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {

	.weather-wrapper h2, .weather-wrapper h1{
		font-size: 24px !important;
		line-height: 40px !important;
	}

	.forecast-wrapper{
		-webkit-flex-wrap: wrap !important;
		        flex-wrap: wrap !important;
		-webkit-justify-content: center;
		        justify-content: center; 
	}

	.ui.card{
		width: 220px !important;
	}
}


@media only screen and (max-width : 576px) {
	
	.column-header h2{
		text-align: right;
	}

	.ui.card{
		width: 170px !important;
	}

	.weather-column{
		display: -webkit-flex !important;
		display: flex !important;
		-webkit-flex-direction: row-reverse;
		        flex-direction: row-reverse;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
	}

	.weather-header{
		font-size: 14px !important;
	}	

	.weather-wrapper h4 img{
		height: 22px !important;
	}

	.ui.grid>[class*="four column"].row>.column{width:100%!important}		
}


/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
	.ui.card{
		width: 220px !important;
	}

}

